* {
    box-sizing: border-box;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    margin: 0;
    background-color: #474759;
}


/* * * * * * * * * * * * * * * * * * * * * * * * * * * * 
                    Sign In
* * * * * * * * * * * * * * * * * * * * * * * * * * * * */
.signin-container {
    width: 100vw;
    height: 100vh;
    background-color: #474759;
    display: flex;
    align-items: center;
    justify-content: center;
}

.signin-form {
    color: #F8F8F9;
    background-color: #262637;
    border-radius: 10px;
    box-shadow: 0px 5px 8px rgba(0,0,0,0.2);
    text-align: center;
}

.signin-container form {
    min-height: 200px;
    max-width: 500px;
    background-color: #9292A0;
    padding: 30px;
    border-radius: 0px 0px 10px 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.signin-container form > input {
    outline: 0;
    padding: 10px;
    font-size: 18px;
    border-radius: 5px;
}

.input-box {
    width: 100%;
    border: none;
    margin: 0px 0px 20px 0px;
    opacity: .5;
    -webkit-transition: all .3s;
    transition: all .3s;
}

.input-box:focus {
    opacity: 1;
    box-shadow: 0px 3px 2px rgba(0,0,0,0.2);
}

.submit-button {
    width: 80px;
    border: none;
    color: #CECED4;
    background-color: #262637;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    -webkit-transition: all .2s;
    transition: all .2s;
}

.submit-button:hover {
    background-color: #080811;
    color: #E1E1E5;
    box-shadow: 0px 0px 10px rgba(0,0,0,0.2);
}


/* * * * * * * * * * * * * * * * * * * * * * * * * * * * 
                    User List
* * * * * * * * * * * * * * * * * * * * * * * * * * * * */

.sidebar {
    width: 20%;
    min-height: 100vh;
    background-color: #262637;
    color: #F8F8F9;
    display: flex;
    flex-direction: column;
    position: fixed;
}

.user-list {
    list-style-type: none;
    padding: 20px;
    margin: 0;
}

.user-list-item {
    display: flex;
    margin: 16px;
    justify-content: left;
    align-items: center;
}

.user-list-name {
    padding-left: 10px;
}

.user-status {
    min-width: 14px;
    min-height: 14px;
    max-width: 14px;
    max-height: 14px;
    border-radius: 50%;
}

.user-online {
    background-color: #73EB85;
}

.user-offline {
    background-color: #676779;
}

/* * * * * * * * * * * * * * * * * * * * * * * * * * * * 
                    Messages 
* * * * * * * * * * * * * * * * * * * * * * * * * * * * */
.message-view {
    width: 80%;
    position: absolute;
    right: 0;
}

.message-view ul {
    background-color: #F8F8F9;
    padding-left: 10px;
    padding-bottom: 50px;
}

.message-container {
    list-style-type: none;
    display: flex;
}

.message-time {
    color: #666;
    font-size: 10px;
    margin-left: 10px;
    font-weight: normal;
}

.message {
    padding: 0px 0px 10px 20px;
}

.message-user {
    font-size: 18px;
    font-weight: bold;
    display: block;
    margin-bottom: 10px;
}

.message-form {
    width: 100%;
    justify-content: center;
    align-items: center;
}

.message-form form {
    width: 100%;
}

.message-input {
    width: 100%;
    font-size: 16px;
    border: 0;
    padding: 20px;
    border-top: 2px solid #ADADB7;
    outline: 0;
    position: fixed;
    bottom: 0;
    margin: 0;
}

.message-send {
    display: none;
}

/* * * * * * * * * * * * * * * * * * * * * * * * * * * * 
                    Loader Animation 
* * * * * * * * * * * * * * * * * * * * * * * * * * * * */

.loader-container {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 50%;
}

.loader {
    margin: 0 auto;
    border: 5px solid #9292A0;
    border-radius: 50%;
    border-top: 5px solid #E1E1E5;
    width: 40px;
    height: 40px;
    -webkit-animation: spin 2s linear infinite;
    /* Safari */
    animation: spin 2s linear infinite;
}

/* Safari */
@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
    }
}

@keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
